import React, { Component } from "react";
import ReactDOM from "react-dom";

import {
  List,
  NavBar,
  Icon,
  Switch,
  Toast,
  PullToRefresh,
  ListView
} from "antd-mobile";
import { setStore, getStore, removeStore } from "util/store";
import { StickyContainer, Sticky } from "react-sticky";

const Item = List.Item;
const Brief = Item.Brief;

import { setValue } from "@/redux/collection-word";
import { connect } from "react-redux";
import { get } from "util/fetch";
import "./index.less";
const colorTheme = {
  0: "#e84118",
  1: "#00a8ff",
  2: "#fbc531",
  3: "#4cd137",
  4: "#487eb0",
  5: "#9c88ff"
};
const getUrlVars = url => {
  var vars = {};
  var parts = url.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m, key, value) {
    vars[key] = value;
  });
  return vars;
};

const routePath = ["/api/english/collection", "/api/english/collection-word"];

class index extends Component {
  state = {
    dataBlob: {},
    dataSource: new ListView.DataSource({
      rowHasChanged: (r1, r2) => r1 !== r2,
      sectionHeaderHasChanged: (s1, s2) => s1 !== s2
    }),

    collection_id: "",
    collection: {},
    total: 0,
    collection_words: [],
    isTrain: false,
    height: document.documentElement.clientHeight,
    refreshing: true,
    isLoading: true,
    pageSize: 5,
    pageNo: 1
  };
  componentDidMount() {
    const hei = this.state.height - ReactDOM.findDOMNode(this.lv).offsetTop;

    this.setState({
      height: hei
    });
    this.rData = [];
    this.init();
  }
  init = () => {
    let query = this.props.location.search;
    let collection_id = getUrlVars(query)["collection_id"];
    let token = getUrlVars(query)["token"];
    if (token === undefined) {
      Toast.info("授权失效", 2);
      return;
    }
    setStore("token", token);
    this.state.collection_id = collection_id;
    get(`${routePath[0]}/${collection_id}`).then(res => {
      this.setState({
        collection: res.data[0]
      });
    });
    this.fetchWords();
  };
  fetchWords = () => {
    this.setState({
      isLoading: true
    });
    let params = {
      collection_id: this.state.collection_id,
      pageSize: this.state.pageSize,
      pageNo: this.state.pageNo
    };
    get(`${routePath[1]}`, params)
      .then(res => {
        let collection_words = [...this.state.collection_words, ...res.data];
        this.rData = res.data.map((item, index) => {
          return index + (this.state.pageNo - 1) * this.state.pageSize;
        });
        var tempDataBlob = this.state.dataBlob;
        tempDataBlob[`${this.state.pageNo}`] = this.rData;

        this.setState({
          dataBlob: tempDataBlob,
          dataSource: this.state.dataSource.cloneWithRowsAndSections(
            tempDataBlob
          ),
          collection_words,
          total: res.count,
          pageNo: parseInt(res.pageNo) + 1,
          isLoading: false
        });
        this.props.setValue({ data: collection_words });
      })
      .catch(err => {
        Toast.info("认证失效", 2);
      });
  };
  handleClick = id => {
    const { isTrain } = this.state;
    if (isTrain) {
      this.props.history.push(`/train-word?collection_id=${id}`);
    } else {
      this.props.history.push(`/learn-word?collection_id=${id}`);
    }
  };
  gotoLast = () => {
    this.props.history.go(-1);
  };
  onEndReached = e => {
    // log(56, e);
    let { total, pageNo, pageSize } = this.state;
    if (pageNo * pageSize - pageSize >= total) {
      return;
    }
    this.fetchWords();
  };
  onScroll = e => {
    // log(e, this.lv);
    if (
      this.lv.listviewRef.scrollProperties.offset +
        this.lv.listviewRef.scrollProperties.visibleLength >=
      this.lv.listviewRef.scrollProperties.contentLength
    ) {
      this.onEndReached();
    }
  };
  onRefresh = () => {};
  render() {
    const navBar = (
      <NavBar
        mode="light"
        onLeftClick={this.gotoLast}
        leftContent={<Icon type="left" />}
        rightContent={[<Icon key="1" type="ellipsis" />]}
      >
        That will get it
      </NavBar>
    );
    const header = (
      <div className="header-container">
        <h2>词汇库</h2>
        <div className="header">
          <div className="title">
            {this.state.collection.name}
            <p>
              {this.state.collection.note} ( total:{this.state.total})
            </p>
          </div>
          <div className="switch">
            复习与否
            <Switch
              checked={this.state.isTrain}
              onChange={() => {
                this.setState({
                  isTrain: !this.state.isTrain
                });
              }}
            />
          </div>
        </div>
      </div>
    );
    const list = (
      <div className="list">
        {this.state.collection_words.map(item => (
          <div
            className="cell"
            key={JSON.stringify(item)}
            onClick={() => this.handleClick(item.id)}
          >
            <div>
              <p>
                {item.word}{" "}
                <span className="grey">[{item.phonetic_symbol}]</span>{" "}
              </p>
              <p className="grey">{item.definition}</p>
            </div>
          </div>
        ))}
      </div>
    );

    const row = (rowData, sectionID, rowID) => {
      return (
        <div
          className="cell"
          onClick={() =>
            this.handleClick(this.state.collection_words[parseInt(rowData)].id)
          }
        >
          <div>
            <p>
              {this.state.collection_words[parseInt(rowData)].word}{" "}
              <span className="grey">
                [
                {this.state.collection_words[parseInt(rowData)].phonetic_symbol}
                ]
              </span>{" "}
            </p>
            <p className="grey">
              {this.state.collection_words[parseInt(rowData)].definition}
            </p>
          </div>
        </div>
      );
    };
    const row1 = rowData => <div>{rowData}</div>;

    const listview = (
      <ListView
        useBodyScroll
        renderSectionWrapper={sectionID => (
          <StickyContainer
            key={`s_${sectionID}_c`}
            className="sticky-container"
            style={{ zIndex: 4 }}
          />
        )}
        renderSectionHeader={(sectionData, sectionID) => {
          return (
            <Sticky>
              {({ style }) => (
                <div
                  className="sticky"
                  style={{
                    ...style,
                    zIndex: 3,
                    backgroundColor: colorTheme[parseInt(sectionID) % 5],
                    color: "white"
                  }}
                >
                  第 {sectionID} 组
                </div>
              )}
            </Sticky>
          );
        }}
        className="list"
        ref={el => (this.lv = el)}
        dataSource={this.state.dataSource}
        style={{ height: this.state.height }}
        renderRow={row}
        pageSize={5}
        initialListSize={10}
        onEndReached={this.onEndReached}
        onEndReachedThreshold={200}
        onScroll={this.onScroll}
        renderFooter={() => (
          <div style={{ lineHeight: "30px", textAlign: "center" }}>
            {this.state.isLoading ? "Loading..." : "到达底部"}
          </div>
        )}
      />
    );
    return (
      <div className="share-word-container">
        {navBar}
        {header}
        {listview}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  redux: state.collectionWord
});
const mapDispatchToProps = dispatch => {
  return {
    setValue: value => {
      dispatch(setValue(value));
    }
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(index);
